.btn-dineIn {
  width: 180px;
  height: 72px;
  border: none;
  background: #f4f4f8;
  border-radius: 20px;
  font-family: Rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #9f9f9f;
  border: 1px solid rgba(186, 186, 186, 0.35);
  margin-right: 15px;
}

.btn-dineIn:focus {
  background: #ffba33;
  color: #6a4029;
  box-shadow: 0px 6px 20px rgba(137, 85, 55, 0.4);
}

