.checkbox{
    display: flex;
    justify-content: flex-end;
}
.checkbox input[type="checkbox"]{
     visibility: hidden;
}
.checkbox label{
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    text-decoration-line: underline;
    color: #FFFFFF;
    text-align: end;
    cursor: pointer;
}
.body-section{
    display: none;
}
.arr:hover{
    cursor: pointer;
}
.handimg{
    display: none;
}
.checkbox > button{
    text-decoration: underline;
    background: none;
    color: white;
    border: none;
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
    margin-left: 10px;
    margin-bottom: 10px;
}
.checkbox > button:hover{
    color: red;
}


@media (max-width: 575.98px) {
    .history{
        background: #F5F5F8;
    }
    .head-section{
        display:flex;
        margin-bottom: 2em;
    }
    .title-history{
        display: none;
    }
    .sub-title-history{
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        color: #000000;
        text-shadow: none;
    }
    .checkbox{
        display: none;
    }
    .body-section{
        display: flex;
        justify-content: space-between;
        margin: 0 .5em;
        margin-top: 3em;
    }
    .handimg{
        display: inline-block;
    }

}
