.cards-profile {
  height: 1400px;
}

.product-box {
  width: 390px;
  height: 500px;
  background-color: rgba(255, 203, 101, 1);
  border-radius: 20px;
}

.img-product {
  height: 500px;
  width: 100%;
  /* border-radius: 50%;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    top: 40px; */
}

p.product-title {
  font-family: Poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 101.34%;
  text-align: center;
  color: #000000;
  margin-top: 10px;
}

p.product {
  font-family: Poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 101.34%;
  text-align: center;
  color: #000000;
  margin-top: 5px;
}

p.product-detail {
  font-style: normal;
  font-weight: 200;
  font-size: 25px;
  line-height: 101.34%;
  text-align: center;
  color: #000000;
  margin-top: 70px;
}

.line {
  width: 390px;
  border: 1px dashed #000000;
  margin-top: 100px;
}

p.product-teks {
  font-weight: normal;
  font-size: 25px;
  line-height: 101.34%;
  text-align: center;
  color: #000000;
  margin-top: 10px;
}

p.product-size {
  font-family: Poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 101.34%;
  text-align: center;
  color: #000000;
  margin-top: 35px;
}

.product-stock {
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 101.34%;
  text-align: center;
  color: #000000;
}

.title {
  font-family: Rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  color: #6a4029;
  margin-top: 50px;
}

.titleExpire {
  font-family: Rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  color: #6a4029;
  margin-top: 35px;
}

.formData .label-product {
  font-family: Rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  color: #6a4029;
  margin-top: 5px;
}

input.input-product-code {
  width: 390px;
  height: 50px;
  background-color: transparent;
  border-top: none;
  border-left: none;
  border-right: none;
}

input.input-image {
  width: 390px;
  background-color: transparent;
  border-top: none;
  border-left: none;
  border-right: none;
}

select.input-product-code {
  width: 390px;
  height: 50px;
  background-color: transparent;
  border-top: none;
  border-left: none;
  border-right: none;
}

.btn-select {
  height: 83px;
  width: 391px;
  border-radius: 20px;
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  text-align: start;
  padding-left: 40px;
  line-height: 138.84%;
  letter-spacing: 0.03em;
  border: 1px solid #9f9f9f;
  box-sizing: border-box;
  margin-bottom: 30px;
}

.dropdown-product {
  width: 390px;
}

@media only screen and (max-width: 600px) {
  .cards-profile {
    background: #f5f5f8;
    height: 1200px;
    padding-top: 40px;
    padding-right: 20px;
    padding-left: 30px;
  }

  .big-circle,
  .img-product {
    height: 132px;
    width: 132px;
  }
}
